   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

html,
body {
   font-size: 62.5%;
   height: 100vh;
   width: 100vw;
}

body {
   font-family: "Raleway", sans-serif;
   color: #333;
   font-weight: 400;
   line-height: 1;
}

#content {
   width: 100%;
}

/* HEADER & NAV SECTION */
#header {
   display: flex;
   flex-direction: column;

   position: sticky;
   top: 0;
   width: 100vw;
   z-index: 10;
   padding: 3rem 0;

   background-color: #fff;
}

#logo {
   font-size: 4rem;
   letter-spacing: 1rem;
   margin-bottom: 4rem;
   text-align: center;
   font-weight: bold;
   cursor: pointer;
}

#nav {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 5rem;
}

#nav > button {
   border: none;
   background-color: transparent;
   font-size: 1.4rem;
   font-weight: bold;
   text-transform: uppercase;
   letter-spacing: 0.4rem;
   color: inherit;
   cursor: pointer;
}

#nav > button:hover {
   color: #ef4b4d;
}

/* HERO SECTION */
.hero-section {
   position: relative;
   height: 100vh;
   width: 100vw;
}

.hero-image-box {
   height: 100%;
   width: 100%;
   background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) !important;
   background-size: cover;
   background-position: center;
   background-repeat: no-repeat;
}

.hero-text-box {
   color: #fff;
   position: absolute;
   top: 35%;
   left: 50%;
   transform: translate(-50%, 0);
}

.hero-text {
   font-size: 6rem;
   font-family: "TrendSansW00-Four";
}

.hero-btns {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, 0);

   display: flex;
   gap: 2rem;
   margin-top: 4rem;
}

.hero-btn {
   border: 3px solid #fff;
   font-size: 1.6rem;
   font-weight: bold;
   text-transform: uppercase;
   letter-spacing: 0.6rem;
   padding: 1rem 3rem;
   background-color: transparent;
   color: #fff;
   cursor: pointer;
   transition: all 0.2s ease-in-out;
}

.hero-btn:hover {
   background-color: #fff;
   color: #0a1736;
}

.footer {
   display: flex;
   justify-content: center;
   align-items: center;
   color: #0a1736;
}

/* MENU SECTION */
.menu-section {
   background-color: #ef4b4d;
   color: #fff;
   padding: 4.8rem 9.6rem;
}

.menu {
   display: flex;
   flex-direction: column;
   padding: 4.8rem 9.6rem;
   border: 10px solid #fff;
}

.heading-secondary {
   font-size: 4rem;
   letter-spacing: 1rem;
   margin-bottom: 1.2rem;
}

.heading-text {
   font-size: 1.4rem;
}

.menu-heading-text {
   font-size: 3rem;
   text-transform: uppercase;
   letter-spacing: 1rem;
   margin-bottom: 1rem;
}

.menu-heading {
   margin-bottom: 5rem;
   margin-top: 5rem;
}

.menu-heading-description {
   font-size: 1.4rem;
}

.menu-items {
   display: flex;
   flex-direction: column;
   gap: 3rem;
}

.menu-item {
   display: flex;
   justify-content: space-between;
   align-items: flex-start;
   margin-bottom: 20px;
}

.menu-item-left {
   flex: 1;
   padding-right: 20px;
}

.menu-price {
   flex-shrink: 0;
   font-weight: bold;
}

.menu-subheading {
   font-size: 1.8rem;
   font-weight: bold;
   letter-spacing: 0.2rem;
   margin-bottom: 1rem;
}

.menu-text {
   font-size: 1.4rem;
}

.menu-price {
   font-size: 1.6rem;
   font-weight: bold;
}

/* CONTACT US SECTION */
.contact-section {
   display: flex;
   justify-content: space-between;
   align-items: flex-start;
   padding: 40px;
   background-color: #f4f4f4;
}

/* Contact Form Styles */
.contact-form {
   width: 48%;
   background-color: white;
   padding: 30px;
   box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
   border-radius: 8px;
}

.contact-heading {
   margin-bottom: 20px;
   font-size: 28px;
}

.contact-form input,
.contact-form textarea,
.send-msg-btn {
   width: 100%;
   padding: 15px;
   margin-bottom: 15px;
   border: 1px solid #ccc;
   border-radius: 5px;
   font-size: 16px;
}

.send-msg-btn {
   background-color: #00c3ad;
   color: white;
   border: none;
   cursor: pointer;
   font-size: 18px;
   transition: background-color 0.3s ease;
}

.send-msg-btn:hover {
   background-color: #00c396;
}

/* Location Map Styles */
.map-container {
   width: 48%;
   height: 400px;
   box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
   border-radius: 8px;
   overflow: hidden;
}

iframe {
   width: 100%;
   height: 100%;
   border: none;
}
